* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}

.app {
  
}

.portal {
  background-image: url('images/bg.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  
}

.overlay{
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;

  backdrop-filter: blur(3px);

}

.topBuffer {
  height:10vh;
  min-height:10vh;
}

.appBox {
  width: 20vw;
  height:auto;
  margin: auto;
  background-color: #FFFFFF;
  border-radius: 1vh;
  border:1px solid rgb(230,230,230);
  box-shadow: rgba(0, 0, 0, 0.1) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
  padding-bottom:1vh;
}

.appBox:hover {
  box-shadow: rgba(0, 0, 0, 0.2) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

.appBox-bannerimage {
  width:20vw;
  height:10vh;
  
  background-image: url('images/ath-eleph.jpg');
  background-size:cover;
  background-repeat: no-repeat;
  border-top-left-radius: 1vh;
  border-top-right-radius: 1vh;
  border-bottom:1px solid rgb(230,230,230);
}

.appBox-title {

  text-align: center;
  font-family: '-apple-system', 'BlinkMacSystemFont', 'Gadugi', 'Trebuchet MS', 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 12pt;
  font-weight: bold;
  background-color: #FFFFFF;
  color: rgb(83, 83, 83);
  padding-top: 1.5vh;
  padding-bottom: 2vh;
  padding-left: 2vw;
  padding-right: 2vw;
}

.appBox-title h3 {
  text-decoration: underline;
  padding-top:1vh;
  padding-bottom:1vh;

}

.appBox-title h6 {
  font-weight: normal;
}

.appBox-title span {
  font-size:1.5vh;
  font-weight: 100;
  
}

.appBox-title p {
  margin-bottom:0.5vh;
}

.appBox-inside {

  width: auto;
  margin-left: 1vw;
  margin-right: 1vw;
  margin-bottom: 1vw;
  text-align: center;
  background-color: #FFFFFF;
  font-family: '-apple-system', 'BlinkMacSystemFont', 'Gadugi', 'Trebuchet MS', 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  color: #333;
  position: relative;
  border-radius: 10px;
  padding-bottom: 1vw;
  padding-left: 1vw;
  padding-right: 1vw;
  font-size: 12pt;

}


.appBox-links {
  width: auto;
  padding-top:2vh;
  padding-bottom:1vh;
  text-align: center;
  background-color: #FFFFFF;
  font-family: '-apple-system', 'BlinkMacSystemFont', 'Gadugi', 'Trebuchet MS', 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  color: #333;
  border-top:1px solid rgb(230,230,230);
}

.appBox-links img {
  width:2vw;
  margin:0.2vw;
  
  
}